import React from 'react';
import { useParams } from 'react-router-dom';

const BlogDetail = () => {
  const { id } = useParams();

  return (
    <div className="container my-5">
      <h2 className="text-center">   {id}</h2>
      <p>      {id}    .</p>
    </div>
  );
};

export default BlogDetail;
