import React, { useState } from 'react';
import Navbar from './Navbar';
import Footer from './Footer';

const DogBreedsArticle = () => {
  const [formData, setFormData] = useState({ name: '', email: '', comment: '' });
  const [errors, setErrors] = useState({});
  const [submitSuccess, setSubmitSuccess] = useState(false);

  const validate = () => {
    const errors = {};
    if (!formData.name.trim()) errors.name = '     .';
    if (!formData.email.trim()) {
      errors.email = '     .';
    } else if (!/^[\w-.]+@([\w-]+\.)+[\w-]{2,4}$/.test(formData.email)) {
      errors.email = '     .';
    }
    if (!formData.comment.trim()) errors.comment = '     .';
    return errors;
  };

  const handleChange = (e) => {
    setFormData(prev => ({ ...prev, [e.target.name]: e.target.value }));
    setErrors(prev => ({ ...prev, [e.target.name]: '' }));
    setSubmitSuccess(false);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const validationErrors = validate();
    if (Object.keys(validationErrors).length > 0) {
      setErrors(validationErrors);
      setSubmitSuccess(false);
    } else {
      //             
      console.log('  :', formData);
      setErrors({});
      setSubmitSuccess(true);
      setFormData({ name: '', email: '', comment: '' });
    }
  };

  return (
    <>
      

      {/*    */}
      <div className="container my-5 d-flex justify-content-center">
        <div
          className="card mb-2"
          style={{ maxWidth: '700px', width: '100%', marginBottom: '0.7rem' }}
        >
          <img
            src="/images/blog1.jpg"
            className="card-img-top"
            alt="  "
            style={{ height: '500px', objectFit: 'cover', cursor: 'default' }}
          />
        </div>
      </div>

      {/*   */}
      <div className="container d-flex justify-content-center">
        <div
          className="card p-4"
          style={{
            maxWidth: '900px',
            width: '100%',
            marginBottom: '3rem',
            cursor: 'default',
            backgroundColor: '#fdfdfd',
            boxShadow: '0 0 20px rgba(0, 0, 0, 0.05)',
            borderRadius: '1rem',
          }}
        >
          <h2
            className="mb-4 text-center"
            style={{ fontSize: '2.3rem', color: '#2c3e50', fontWeight: '700' }}
          >
               
          </h2>

          <p style={{ color: '#444', lineHeight: '1.9', fontSize: '1.1rem' }}>
                          .                     .
          </p>

          {[
            { title: '.  ', text: '                     .                   .' },
            { title: '.  ', text: '           .              .               .' },
            { title: '. ', text: '                       .             .' },
            { title: '.  ', text: '                 .          .' },
            { title: '.  ', text: '     .                        .' },
            { title: '. ', text: '          .                .' }
          ].map((breed, index) => (
            <div key={index}>
              <h5
                style={{
                  color: '#34495e',
                  fontWeight: '600',
                  marginTop: '1.8rem',
                  fontSize: '1.15rem'
                }}
              >
                {breed.title}
              </h5>
              <p style={{ color: '#444', lineHeight: '1.9', fontSize: '1rem' }}>
                {breed.text}
              </p>
            </div>
          ))}

          <p
            style={{
              color: '#444',
              lineHeight: '1.9',
              marginTop: '2rem',
              fontSize: '1.05rem'
            }}
          >
                                       .                   .
          </p>
        </div>
      </div>

      {/*    */}
      <div className="container d-flex justify-content-center">
        <div
          className="card p-4"
          style={{
            maxWidth: '700px',
            width: '100%',
            marginBottom: '10rem',
            borderRadius: '1rem',
            backgroundColor: '#ffffff',
            boxShadow: '0 0 15px rgba(0,0,0,0.05)',
            cursor: 'default'
          }}
        >
          <h3
            style={{
              textAlign: 'center',
              marginBottom: '1.5rem',
              color: '#2c3e50',
              fontWeight: '700',
            }}
          >
              
          </h3>

          {submitSuccess && (
            <p
              style={{
                color: 'green',
                textAlign: 'center',
                marginBottom: '1rem',
                fontWeight: '600',
              }}
            >
                   .    !
            </p>
          )}

          <form onSubmit={handleSubmit} noValidate>
            {/*  */}
            <div className="mb-3">
              <label htmlFor="name" className="form-label" style={{ fontWeight: '600' }}>
                :
              </label>
              <input
                type="text"
                id="name"
                name="name"
                className={`form-control ${errors.name ? 'is-invalid' : ''}`}
                value={formData.name}
                onChange={handleChange}
                placeholder="    "
              />
              {errors.name && (
                <div className="invalid-feedback">{errors.name}</div>
              )}
            </div>

            {/*  */}
            <div className="mb-3">
              <label htmlFor="email" className="form-label" style={{ fontWeight: '600' }}>
                :
              </label>
              <input
                type="email"
                id="email"
                name="email"
                className={`form-control ${errors.email ? 'is-invalid' : ''}`}
                value={formData.email}
                onChange={handleChange}
                placeholder="    "
              />
              {errors.email && (
                <div className="invalid-feedback">{errors.email}</div>
              )}
            </div>

            {/*  */}
            <div className="mb-3">
              <label htmlFor="comment" className="form-label" style={{ fontWeight: '600' }}>
                :
              </label>
              <textarea
                id="comment"
                name="comment"
                className={`form-control ${errors.comment ? 'is-invalid' : ''}`}
                rows="5"
                value={formData.comment}
                onChange={handleChange}
                placeholder="    "
              ></textarea>
              {errors.comment && (
                <div className="invalid-feedback">{errors.comment}</div>
              )}
            </div>

            <button
              type="submit"
              className="btn short-cute-btn mt-2"
              style={{ fontWeight: '600' }}
            >
               
            </button>
          </form>
        </div>
      </div>

      
    </>
  );
};

export default DogBreedsArticle;
