import React, { useState } from 'react';
import { FaFacebookF, FaTwitter, FaYoutube } from 'react-icons/fa';

const Contact = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    message: ''
  });

  const [errors, setErrors] = useState({});

  const validate = () => {
    const newErrors = {};
    if (!formData.name.trim()) newErrors.name = '       .';
    if (!formData.email.trim()) {
      newErrors.email = '    .';
    } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
      newErrors.email = '     .';
    }
    if (!formData.subject.trim()) newErrors.subject = '     .';
    if (!formData.message.trim()) newErrors.message = '    .';

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = e => {
    e.preventDefault();
    if (validate()) {
      alert('    ! ');
      setFormData({ name: '', email: '', subject: '', message: '' });
      setErrors({});
    }
  };

  const handleChange = e => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  return (
    <div className="container my-5">
      <div className="row">
        {/*   */}
        <div className="col-lg-6 mb-5">
          <h1 className="mb-3">  </h1>
          <p className="mb-4">
                 <br />
                              .
          </p>
          <h4 className="mb-3">    .</h4>
          <div className="mb-4 d-flex gap-3">
            <a href="https://facebook.com" target="_blank" rel="noreferrer" className="text-decoration-none text-primary fs-4">
              <FaFacebookF />
            </a>
            <a href="https://twitter.com" target="_blank" rel="noreferrer" className="text-decoration-none text-info fs-4">
              <FaTwitter />
            </a>
            <a href="https://youtube.com" target="_blank" rel="noreferrer" className="text-decoration-none text-danger fs-4">
              <FaYoutube />
            </a>
          </div>
          <div className="d-flex flex-column gap-3">
            <div className="card shadow-sm p-3" style={{
              borderRadius: '12px',
              backgroundColor: '#c6450d8d',
              maxWidth: '320px',
              width: '100%',
              transition: 'transform 0.2s ease',
              cursor: 'default'
            }}
              onMouseEnter={e => e.currentTarget.style.transform = 'scale(1.03)'}
              onMouseLeave={e => e.currentTarget.style.transform = 'scale(1)'}
            >
              <h5 style={{ fontWeight: '600', color: '#4d0707' }}> </h5>
              <p style={{ color: '#190202', marginBottom: 0 }}>  </p>
            </div>

            <div className="card shadow-sm p-3" style={{
              borderRadius: '12px',
              backgroundColor: '#e4335c93',
              maxWidth: '320px',
              width: '100%',
              transition: 'transform 0.2s ease',
              cursor: 'default'
            }}
              onMouseEnter={e => e.currentTarget.style.transform = 'scale(1.03)'}
              onMouseLeave={e => e.currentTarget.style.transform = 'scale(1)'}
            >
              <h5 style={{ fontWeight: '600', color: '#38042a' }}></h5>
              <p style={{ color: '#32011c', marginBottom: 0 }}>      </p>
            </div>

            <div className="card shadow-sm p-3" style={{
              borderRadius: '12px',
              backgroundColor: '#5dba8295',
              maxWidth: '320px',
              width: '100%',
              transition: 'transform 0.2s ease',
              cursor: 'default'
            }}
              onMouseEnter={e => e.currentTarget.style.transform = 'scale(1.03)'}
              onMouseLeave={e => e.currentTarget.style.transform = 'scale(1)'}
            >
              <h5 style={{ fontWeight: '600', color: '#075219' }}> </h5>
              <p style={{ color: '#064a3a', marginBottom: 0 }}>
                     <br />
                   
              </p>
            </div>
          </div>
        </div>

        {/*   -   */}
        <div className="col-lg-6">
          <form onSubmit={handleSubmit} noValidate>
            <div className="mb-3">
              <label className="form-label">   </label>
              <input
                type="text"
                name="name"
                value={formData.name}
                onChange={handleChange}
                className={`form-control ${errors.name ? 'is-invalid' : ''}`}
                placeholder="   "
              />
              {errors.name && <div className="invalid-feedback">{errors.name}</div>}
            </div>

            <div className="mb-3">
              <label className="form-label"></label>
              <input
                type="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                className={`form-control ${errors.email ? 'is-invalid' : ''}`}
                placeholder=""
              />
              {errors.email && <div className="invalid-feedback">{errors.email}</div>}
            </div>

            <div className="mb-3">
              <label className="form-label"></label>
              <input
                type="text"
                name="subject"
                value={formData.subject}
                onChange={handleChange}
                className={`form-control ${errors.subject ? 'is-invalid' : ''}`}
                placeholder=" "
              />
              {errors.subject && <div className="invalid-feedback">{errors.subject}</div>}
            </div>

            <div className="mb-3">
              <label className="form-label"></label>
              <textarea
                name="message"
                value={formData.message}
                onChange={handleChange}
                className={`form-control ${errors.message ? 'is-invalid' : ''}`}
                rows="5"
                placeholder="    "
              ></textarea>
              {errors.message && <div className="invalid-feedback">{errors.message}</div>}
            </div>

            <button type="submit" className="btn short-cute-btn mt-2"></button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Contact;
