import React, { useState } from 'react';

const CommentForm = () => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [website, setWebsite] = useState('');
  const [comment, setComment] = useState('');
  const [remember, setRemember] = useState(false);
  const [errors, setErrors] = useState({});
  const [successMessage, setSuccessMessage] = useState('');
  const [errorMessage, setErrorMessage] = useState(''); //    

  const validate = () => {
    const newErrors = {};
    if (!comment.trim()) newErrors.comment = '     .';
    if (!name.trim()) newErrors.name = '     .';
    if (!email.trim()) newErrors.email = '     .';
    else if (!/\S+@\S+\.\S+/.test(email)) newErrors.email = '   .';
    return newErrors;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const validationErrors = validate();
    setErrors(validationErrors);

    if (Object.keys(validationErrors).length > 0) {
      setErrorMessage('        .');
      setSuccessMessage('');
      return;
    }

    // 
    setSuccessMessage('     .  ! ');
    setErrorMessage('');

    //  
    setName('');
    setEmail('');
    setWebsite('');
    setComment('');
    setRemember(false);
    setErrors({});

    //       
    setTimeout(() => {
      setSuccessMessage('');
    }, 6000);
  };

  return (
    <div className="container my-5">
      <h2 className="text-center mb-4"> </h2>

      {/*    */}
      {successMessage && (
        <div className="alert alert-success text-center fw-bold" role="alert">
          {successMessage}
        </div>
      )}

      {/*     */}
      {errorMessage && (
        <div className="alert alert-danger text-center fw-bold" role="alert">
          {errorMessage}
        </div>
      )}

      <form onSubmit={handleSubmit} className="w-100 w-md-75 mx-auto">
        <div className="mb-3">
          <label className="form-label"></label>
          <textarea
            className={`form-control ${errors.comment ? 'is-invalid' : ''}`}
            rows="4"
            value={comment}
            onChange={(e) => setComment(e.target.value)}
          ></textarea>
          {errors.comment && <div className="invalid-feedback">{errors.comment}</div>}
        </div>

        <div className="mb-3">
          <label className="form-label"> </label>
          <input
            type="text"
            className={`form-control ${errors.name ? 'is-invalid' : ''}`}
            value={name}
            onChange={(e) => setName(e.target.value)}
          />
          {errors.name && <div className="invalid-feedback">{errors.name}</div>}
        </div>

        <div className="mb-3">
          <label className="form-label"></label>
          <input
            type="email"
            className={`form-control ${errors.email ? 'is-invalid' : ''}`}
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          {errors.email && <div className="invalid-feedback">{errors.email}</div>}
        </div>

        <div className="mb-3">
          <label className="form-label"></label>
          <input
            className="form-control"
            value={website}
            onChange={(e) => setWebsite(e.target.value)}
          />
        </div>

        <div className="form-check mb-4" dir="rtl">
          <label className="form-check-label d-flex align-items-center" htmlFor="rememberInfo">
            <input
              className="form-check-input ms-2"
              type="checkbox"
              checked={remember}
              onChange={(e) => setRemember(e.target.checked)}
              id="rememberInfo"
            />
                         .
          </label>
        </div>

        <button
          type="submit"
          className="btn short-cute-btn px-4 py-2 fw-bold rounded-3"
          style={{ fontSize: '1rem', letterSpacing: '0.5px' }}
        >
           
        </button>
      </form>
    </div>
  );
};

export default CommentForm;
