import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import "./ShopPage.css";

const products = [
  { id: 1, name: '   ', category: '', price: '980,000 ', image: '/images/  1.jpg', description: '         .' },
  { id: 2, name: '   ', category: '', price: '980,000 ', image: '/images/  2.jpg', description: '           .' },
  { id: 3, name: '    1', category: ' ', price: '320,000 ', image: '/images/cat1.jpg', description: '       .' },
  { id: 4, name: '    2', category: ' ', price: '75,000 ', image: '/images/cat2.jpg', description: '        .' },
  { id: 5, name: '    3', category: ' ', price: '95,000 ', image: '/images/cat3.jpg', description: '      .' },
  { id: 6, name: '    4', category: ' ', price: '400,000 ', image: '/images/cat4.jpg', description: '    .' },
  { id: 7, name: '    5', category: ' ', price: '120,000 ', image: '/images/cat5.jpg', description: '       .' },
  { id: 8, name: '    6', category: ' ', price: '210,000 ', image: '/images/cat6.jpg', description: '      .' },
  { id: 9, name: '  ', category: ' ', price: '310,000 ', image: '/images/cart7.jpg', description: '     .' },
  { id: 10, name: '  ', category: '', price: '40,000 ', image: '/images/--.jpg', description: '     .' },
  { id: 11, name: '   ', category: ' ', price: '550,000 ', image: '/images/cart9.jpg', description: '      .' },
  { id: 12, name: '  ', category: '', price: '90,000 ', image: '/images/cart10.png', description: '      .' },
  { id: 13, name: '  ', category: '', price: '135,000 ', image: '/images/cart11.jpg', description: '      .' },
  { id: 14, name: '  ', category: '', price: '135,000 ', image: '/images/1.jpg', description: '       .' },
  { id: 15, name: ' ', category: '', price: '135,000 ', image: '/images/2.jpg', description: '       .' },
];

const categories = ['', ' ', ' ', '', '', '', ''];

const ShopPage = () => {
  const location = useLocation();
  const navigate = useNavigate();

  const getCategoryFromURL = () => {
    const params = new URLSearchParams(location.search);
    const category = params.get('category');
    return categories.includes(category) ? category : '';
  };

  const [selectedCategory, setSelectedCategory] = useState(getCategoryFromURL());
  const [hoveredProductId, setHoveredProductId] = useState(null);

  useEffect(() => {
    setSelectedCategory(getCategoryFromURL());
  }, [location.search]);

  const filteredProducts = selectedCategory === ''
    ? products
    : products.filter(product => product.category === selectedCategory);

  const addToCart = (product) => {
    const cartItems = JSON.parse(localStorage.getItem('cart')) || [];
    const existing = cartItems.find(item => item.id === product.id);

    if (existing) {
      existing.quantity += 1;
    } else {
      cartItems.push({ ...product, quantity: 1 });
    }

    localStorage.setItem('cart', JSON.stringify(cartItems));

    toast.success(
      <div>
         {product.name}     !
        <div className="mt-2">
          <button className="btn btn-sm btn-primary" onClick={() => {
            navigate("/cart");
            toast.dismiss();
          }}>
              
          </button>
        </div>
      </div>,
      {
        position: "bottom-left",
        autoClose: 5000,
        closeOnClick: false,
      }
    );
  };

  return (
    <div className="container my-5">
      <h2 className="text-center mb-4 fw-bold">  </h2>
      <div className="row">
        <div className="col-md-3 mb-4">
          <div className="card shadow-sm p-3">
            <h5 className="fw-bold mb-3">   </h5>
            {categories.map(category => (
              <button
                key={category}
                className={`btn btn-sm mb-2 w-100 text-start ${selectedCategory === category ? 'btn-primary text-white' : 'btn-outline-secondary'}`}
                onClick={() => setSelectedCategory(category)}
              >
                {category}
              </button>
            ))}
          </div>
        </div>

        <div className="col-md-9">
          <div className="row g-4">
            {filteredProducts.length > 0 ? (
              filteredProducts.map(product => (
                <div className="col-sm-6 col-md-4" key={product.id}>
                  <div
                    className="card-wrapper position-relative"
                    onMouseEnter={() => setHoveredProductId(product.id)}
                    onMouseLeave={() => setHoveredProductId(null)}
                  >
                    <div className="card h-100 shadow-sm">
                      <img src={product.image} className="card-img-top" alt={product.name} />
                      <div className="card-body d-flex flex-column justify-content-between">
                        <h5 className="card-title">{product.name}</h5>
                        <p className="card-text fw-bold text-success">{product.price}</p>
                        <button className="btn btn-add-buy mt-auto" onClick={() => addToCart(product)}>
                              
                        </button>
                      </div>
                    </div>
                    {hoveredProductId === product.id && (
                      <div className="product-description-hover">
                        {product.description}
                      </div>
                    )}
                  </div>
                </div>
              ))
            ) : (
              <div className="text-center text-muted">   </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ShopPage;


