import React, { useState } from 'react';
import Navbar from './Navbar';
import Footer from './Footer';

const CatBreedsArticle = () => {
  //   
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    comment: '',
  });

  //   
  const [errors, setErrors] = useState({});

  //    
  const [success, setSuccess] = useState(false);

  //  
  const handleChange = (e) => {
    setFormData(prev => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  //  
  const validate = () => {
    const newErrors = {};
    if (!formData.name.trim()) newErrors.name = '  .';
    if (!formData.email.trim()) newErrors.email = '  .';
    else {
      //   
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!emailRegex.test(formData.email)) newErrors.email = '  .';
    }
    if (!formData.comment.trim()) newErrors.comment = '    .';
    return newErrors;
  };

  //  
  const handleSubmit = (e) => {
    e.preventDefault();
    const validationErrors = validate();
    if (Object.keys(validationErrors).length > 0) {
      setErrors(validationErrors);
      setSuccess(false);
    } else {
      setErrors({});
      setSuccess(true);

      //          
      //      
      setFormData({ name: '', email: '', comment: '' });
    }
  };

  return (
    <>
    

      <div className="container my-5 d-flex justify-content-center">
        <div
          className="card mb-2"
          style={{ maxWidth: '700px', width: '100%', cursor: 'default' }}
        >
          <img
            src="/images/blog2.jpg"
            className="card-img-top"
            alt=" "
            style={{ height: '500px', objectFit: 'cover', cursor: 'default' }}
          />
        </div>
      </div>

      <div className="container d-flex justify-content-center">
        <div
          className="card p-4"
          style={{
            maxWidth: '900px',
            width: '100%',
            marginBottom: '5rem',
            cursor: 'default',
            borderRadius: '1rem',
            backgroundColor: '#fcfcfc',
            boxShadow: '0 0 10px rgba(0,0,0,0.05)',
          }}
        >
          <h2
            className="text-center mb-4"
            style={{ fontSize: '2.3rem', fontWeight: '700', color: '#2c3e50' }}
          >
               
          </h2>

          <p style={{ color: '#444', lineHeight: '1.9', fontSize: '1.05rem' }}>
                           ...
          </p>

          {[
            {
              title: '.   ( )',
              text: '          .     .',
            },
            {
              title: '. ',
              text: '        .        .',
            },
            {
              title: '.   ',
              text: '                .',
            },
            {
              title: '.  ',
              text: '           .',
            },
            {
              title: '.  ',
              text: '             .',
            },
          ].map((breed, index) => (
            <div key={index}>
              <h5
                style={{ marginTop: '1.6rem', fontWeight: '600', color: '#34495e' }}
              >
                {breed.title}
              </h5>
              <p style={{ color: '#444', lineHeight: '1.9' }}>{breed.text}</p>
            </div>
          ))}

          <p style={{ marginTop: '2rem', color: '#444', lineHeight: '1.9' }}>
                          . 
          </p>
        </div>
      </div>

      {/*    */}
      <div className="container d-flex justify-content-center">
        <form
          onSubmit={handleSubmit}
          style={{
            maxWidth: '600px',
            width: '100%',
            backgroundColor: '#fff',
            padding: '2rem',
            borderRadius: '1rem',
            boxShadow: '0 0 15px rgba(0,0,0,0.1)',
            marginBottom: '10rem',
          }}
          noValidate
        >
          <h3
            className="mb-4 text-center"
            style={{ color: '#2c3e50', fontWeight: '700' }}
          >
              
          </h3>

          {/*  */}
          <div className="mb-3">
            <label htmlFor="name" className="form-label" style={{ fontWeight: '600' }}>
              :
            </label>
            <input
              type="text"
              id="name"
              name="name"
              className={`form-control ${errors.name ? 'is-invalid' : ''}`}
              value={formData.name}
              onChange={handleChange}
              placeholder="    "
            />
            {errors.name && <div className="invalid-feedback">{errors.name}</div>}
          </div>

          {/*  */}
          <div className="mb-3">
            <label htmlFor="email" className="form-label" style={{ fontWeight: '600' }}>
              :
            </label>
            <input
              type="email"
              id="email"
              name="email"
              className={`form-control ${errors.email ? 'is-invalid' : ''}`}
              value={formData.email}
              onChange={handleChange}
              placeholder="example@example.com"
            />
            {errors.email && <div className="invalid-feedback">{errors.email}</div>}
          </div>

          {/*  */}
          <div className="mb-3">
            <label htmlFor="comment" className="form-label" style={{ fontWeight: '600' }}>
              :
            </label>
            <textarea
              id="comment"
              name="comment"
              className={`form-control ${errors.comment ? 'is-invalid' : ''}`}
              value={formData.comment}
              onChange={handleChange}
              rows="4"
              placeholder="    "
            ></textarea>
            {errors.comment && <div className="invalid-feedback">{errors.comment}</div>}
          </div>

          <button
            type="submit"
            className="btn short-cute-btn mt-2"
            style={{ fontWeight: '700' }}
          >
             
          </button>

          {success && (
            <div
              className="alert alert-success mt-3 text-center"
              role="alert"
              style={{ fontWeight: '600' }}
            >
                   !  
            </div>
          )}
        </form>
      </div>

     
    </>
  );
};

export default CatBreedsArticle;
