import React, { useState } from "react";
import "./OrderComplete.css";
const initialForm = { name: "", phone: "", address: "" };
const initialErrors = { name: "", phone: "", address: "" };

const OrderComplete = () => {
  const [form, setForm] = useState(initialForm);
  const [errors, setErrors] = useState(initialErrors);
  const [submitted, setSubmitted] = useState(false);

  //  
  const validate = () => {
    const newErrors = { ...initialErrors };
    let isValid = true;

    if (!form.name.trim()) {
      newErrors.name = "        .";
      isValid = false;
    }

    if (!form.phone.trim()) {
      newErrors.phone = "      .";
      isValid = false;
    } else {
      //     (   10 )
      const phoneRegex = /^[0-9]{10,}$/;
      if (!phoneRegex.test(form.phone.replace(/\D/g, ""))) {
        newErrors.phone = "     (   10 ).";
        isValid = false;
      }
    }

    if (!form.address.trim()) {
      newErrors.address = "     .";
      isValid = false;
    }

    setErrors(newErrors);
    return isValid;
  };

  const handleChange = (field, value) => {
    setForm((prev) => ({ ...prev, [field]: value }));
    setErrors((prev) => ({ ...prev, [field]: "" })); //      
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validate()) {
      //    
      console.log("  :", form);
      localStorage.removeItem("cart"); //    
      setSubmitted(true);
    }
  };

  //         
  if (submitted) {
    return (
      <div className="order-complete-container container my-5">
        <div className="card shadow p-4 text-center">
          <h2 className="text-success mb-4">      !</h2>
          <p>   .            .</p>
          <button
            className=" btn cute-orange-btn  mt-4"
            onClick={() => (window.location.href = "/")}
          >
               
          </button>
        </div>
      </div>
    );
  }

  //    
  return (
    <div className="order-complete-container container my-5">
      <div className="card shadow p-4">
        <h2 className="text-center mb-4">    </h2>
        <form onSubmit={handleSubmit} noValidate>
          <div className="mb-3">
            <label className="form-label">   </label>
            <input
              type="text"
              className={`form-control ${errors.name ? "is-invalid" : ""}`}
              value={form.name}
              onChange={(e) => handleChange("name", e.target.value)}
            />
            {errors.name && <div className="invalid-feedback">{errors.name}</div>}
          </div>

          <div className="mb-3">
            <label className="form-label"> </label>
            <input
              type="tel"
              className={`form-control ${errors.phone ? "is-invalid" : ""}`}
              value={form.phone}
              onChange={(e) => handleChange("phone", e.target.value)}
            />
            {errors.phone && <div className="invalid-feedback">{errors.phone}</div>}
          </div>

          <div className="mb-3">
            <label className="form-label"> </label>
            <textarea
              className={`form-control ${errors.address ? "is-invalid" : ""}`}
              rows="3"
              value={form.address}
              onChange={(e) => handleChange("address", e.target.value)}
            ></textarea>
            {errors.address && <div className="invalid-feedback">{errors.address}</div>}
          </div>

          <div className="d-grid">
            <button type="submit" className="btn btn-success w-100">
                 
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default OrderComplete;
